html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  color: rgba(33, 56, 66, 0.6);
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1170px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  padding: 24px 0;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo__svg {
  width: 258px;
  height: 40px;
}

.burger {
  position: relative;
  z-index: 10;
  width: 30px;
  height: 20px;
  cursor: pointer;
}

.burger__line {
  position: absolute;
  display: block;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  background-color: #4d533c;
  transition: all 0.4s ease-in-out;
}

.burger__line:nth-child(1) {
  top: 0;
}

.burger__line:nth-child(2),
.burger__line:nth-child(3) {
  top: 8px;
}

.burger__line:nth-child(3) {
  right: 0;
}

.burger__line:nth-child(4) {
  bottom: 0;
}

.burger_active .burger__line:nth-child(1) {
  background-color: transparent;
  transform: translateX(40px);
}

.burger_active .burger__line:nth-child(2) {
  transform: rotate(45deg);
}

.burger_active .burger__line:nth-child(3) {
  transform: rotate(-45deg);
}

.burger_active .burger__line:nth-child(4) {
  background-color: transparent;
  transform: translateX(-40px);
}

.hero {
  padding: 188px 0 213px;
  background-image: url("../img/hero__bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__txt {
  max-width: 616px;
  color: #fff;
}

.hero__overhead {
  font-size: 14px;
}

.hero__title {
  margin: 24px 0 0;
}

.hero__parag {
  margin: 24px 0 0;
  line-height: 26px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.hero__btn {
  display: block;
  margin: 51px 0 0;
}

.title {
  font-weight: 700;
}

.title_size_m {
  font-size: 60px;
  line-height: 74px;
}

.title_size_s {
  font-size: 36px;
  line-height: 46px;
}

.btn {
  display: block;
  font-weight: 500;
  text-decoration: underline;
  color: #d4d6d4;
  transition: 0.3s;
  cursor: pointer;
}

.btn:hover {
  color: #ffd723;
}

.btn_color_revers {
  color: #ffd723;
}

.btn_color_revers:hover {
  color: #d4d6d4;
}

.brands {
  position: relative;
}

.brands__wrap {
  position: absolute;
  right: 0;
  top: -103px;
  display: flex;
  border-radius: 2px;
  padding: 60px 0;
  width: 88.5%;
  max-width: 1276px;
  box-shadow: 50px 50px 100px rgba(0, 0, 0, 0.14);
  background-color: #fff;
}

.brands__img {
  margin: 0 0 0 65px;
  width: 156px;
  height: 113px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.brands__img:nth-child(1) {
  background-image: url("../img/brands__img1.jpg");
}

.brands__img:nth-child(2) {
  background-image: url("../img/brands__img2.jpg");
}

.brands__img:nth-child(3) {
  background-image: url("../img/brands__img3.jpg");
}

.brands__img:nth-child(4) {
  background-image: url("../img/brands__img4.jpg");
}

.brands__img:nth-child(5) {
  background-image: url("../img/brands__img5.jpg");
}

.about {
  padding: 248px 0 0;
}

.about__container {
  border-bottom: 1px solid rgba(33, 56, 66, 0.095);
  padding-bottom: 150px;
}

.about__list {
  display: flex;
  justify-content: space-between;
}

.about__item:first-child {
  max-width: 470px;
}

.about__overhead {
  font-size: 14px;
  color: #4d533c;
}

.about__title {
  margin: 29px 0 0;
  color: #4d533c;
}

.about__parag {
  margin: 35px 0 0;
  line-height: 26px;
}

.about__btn {
  margin: 42px 0 0;
}

.i-selection {
  font-style: normal;
  background-color: rgba(255, 215, 35, 0.1);
}

.comment {
  padding: 0 40px 60px;
  max-width: 474px;
  box-shadow: 0 100px 100px rgba(0, 0, 0, 0.14);
  background-color: #fff;
}

.comment__parag {
  font-style: italic;
  font-size: 24px;
  line-height: 40px;
  color: #4d533c;
}

.pers {
  display: flex;
  align-items: center;
  margin: 50px 0 0;
}

.pers__ava {
  width: 90px;
  height: 90px;
  background-image: url("../img/comment__ava.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.pers__desc {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 30px;
}

.pers__name {
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
  color: #4d533c;
}

.pers__position {
  line-height: 24px;
  color: #152934;
}

@media (max-width: 650px) {
  .container {
    padding: 0 15px;
  }
}